  left: 0;
  right: 0;
  bottom: 0;
  background-color: #000;
}
.fixedbg .mask-fixedbg {
  position: fixed;
  z-index: 111111111111;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
}


